<template>
  <div>
    <div class="container">
      <!--  -->
      <div class="detail">
        <span></span>
        <div class="center_detail">
          <div>
            <span class="Union_what"></span>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="advantage">
        <span></span>
        <div class="footer_detail">
          <ul>
            <li>
              <div>
                <img src="~assets/images/introduced01.png">
                <p class="first_p"></p>
                <p></p>
                <p> </p>
              </div>
            </li>
            <li>
              <div>
                <img src="~assets/images/introduced02.png">
                <p class="first_p"></p>
                <p></p>
                <p> </p>
              </div>
            </li>
            <li>
              <div>
                <img src="~assets/images/introduced03.png">
                <p class="first_p"></p>
                <p>,</p>
                <p> </p>
              </div>
            </li>
            <li>
              <div>
                <img src="~assets/images/introduced04.png">
                <p class="first_p"></p>
                <p></p>
                <p> </p>
              </div>
            </li>
          </ul>
          <div class="btn1">
            <el-button type="primary" @click="create"></el-button>
            <el-button @click="back"></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $http from '@/utils/http.js';
export default {
  name: 'union-introduced',
  methods: {
    create: function() {
      $http
        .get(`/unionMainCreate/checkPermit`)
        .then(res => {
          if (res.data.success && !res.data.data.isPay) {
            this.$store.commit('permitIdChange', res.data.data.permitId);
            this.$router.push({ path: '/my-union/create-step' });
          } else if (res.data.success && res.data.data.isPay) {
            this.$router.push({ path: '/my-union/no-register' });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    back: function() {
      this.$router.push({ path: '/my-union/no-currentUnion' });
    }
  }
};
</script>

<style lang='less' rel="stylesheet/less" scoped>
.container {
  margin: 40px 50px;
  .detail {
    margin-bottom: 50px;
    > span {
      color: #333333;
      font-weight: bold;
    }
    .center_detail {
      margin-top: 20px;
      height: 200px;
      border: 1px solid #eeeeee;
      background: #f8f8f8;
      text-align: center;
      > div {
        margin-top: 48px;
        .Union_what {
          display: block;
          margin-bottom: 20px;
          color: #333333;
        }
        > p {
          font-size: 14px;
          color: #666666;
        }
      }
    }
  }
  .advantage {
    > span {
      color: #333333;
      font-weight: bold;
    }
    .footer_detail {
      margin-top: 20px;
      height: 340px;
      border: 1px solid #f8f8f8;
      box-sizing: border-box;
      background: #f8f8f8;
      > ul {
        margin-top: 55px;
        overflow: hidden;
        > li {
          margin-left: 7%;
          float: left;
          width: 17%;
          text-align: center;
          .first_p {
            margin: 28px 0;
          }
          p {
            font-size: 14px;
          }
        }
      }
      .btn1 {
        text-align: center;
        margin-top: 65px;
      }
    }
  }
}
</style>



