<template>
  <div id="memberName">
    <el-row class="user_search">
      <el-col style="width: 220px">
        <div class="grid-content bg-purple">
          <el-input placeholder="" icon="search" v-model="input" :on-icon-click="search" class="input-search" @keyup.enter.native="search">
          </el-input>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" style="width:190px;padding-left: 20px">
        <el-button type="primary" @click="output"></el-button>
      </el-col>
    </el-row>
    <!--   -->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column label="">
        <template slot-scope="scope">
          <i class="iconfont" v-if="scope.row.member.isUnionOwner" style="font-size: 25px;color:#FDD43F;position: absolute;top: 4px;left: 9px;">&#xe504;</i>
          <span>{{ scope.row.member.enterpriseName }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="member.directorName" label="">
      </el-table-column>
      <el-table-column prop="member.directorPhone" label="">
      </el-table-column>
      <el-table-column prop="member.createTime" label="">
      </el-table-column>
      <el-table-column label="" width="220">
        <template slot-scope="scope">
          <div class="sizeAndColor">
            <el-button size="small" @click="detail(scope)"></el-button>
            <el-button size="small" v-if="isUnionOwner && !scope.row.memberOut.type" @click="remove(scope)"></el-button>
            <el-button size="small" v-if="isUnionOwner && scope.row.memberOut.type === 2" @click="withdraw(scope)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll" v-if="tableData.length>0">
    </el-pagination>
    <!--   -->
    <div class="model__10">
      <el-dialog title="" :visible.sync="visible" size="tiny">
        <hr>
        <div>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="">
              {{ form.enterpriseName }}
            </el-form-item>
            <el-form-item label="">
              {{ form.createTime }}
            </el-form-item>
            <el-form-item label="">
              {{ form.directorName }}
            </el-form-item>
            <el-form-item label="">
              {{ form.directorPhone }}
            </el-form-item>
            <el-form-item label="">
              {{ form.directorEmail }}
            </el-form-item>
            <el-form-item label="">
              {{ form.enterpriseAddress }}
            </el-form-item>
          </el-form>
        </div>
      </el-dialog>
    </div>
    <!--   -->
    <el-dialog title="" :visible.sync="visible" size="tiny">
      <div class="model_12">
        <p> {{ enterpriseName }} </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm1"></el-button>
        <el-button @click="visible1=false"></el-button>
      </span>
    </el-dialog>
    <!--   -->
    <el-dialog title="" :visible.sync="visible" size="tiny">
      <div class="model_12">
        <p> {{ enterpriseName }} </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm2"></el-button>
        <el-button @click="visible2=false"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import { timeFilter } from '@/utils/filter.js';
export default {
  name: 'union-member',
  data() {
    return {
      input: '',
      tableData: [],
      currentPage: 1,
      visible: false,
      form: {
        busAddress: '',
        createTime: '',
        directorEmail: '',
        directorName: '',
        directorPhone: '',
        enterpriseName: ''
      },
      current: 1,
      tgtMemberId: '',
      totalAll: 0,
      visible1: false,
      visible2: false,
      enterpriseName: '',
      memberId: '',
      outId: ''
    };
  },
  computed: {
    unionId() {
      return this.$store.state.unionId;
    },
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    }
  },
  mounted: function() {
    if (this.unionId) {
      this.init();
    }
  },
  activated: function() {
    if (this.unionId) {
      this.init();
    }
  },
  watch: {
    // 
    unionId: function() {
      if (this.unionId) {
        this.init();
      }
    }
  },
  methods: {
    init() {
      $http
        .get(`/unionMember/unionId/${this.unionId}/index/page?current=1`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
            this.tableData.forEach((v, i) => {
              v.member.createTime = timeFilter(v.member.createTime);
            });
            this.totalAll = res.data.data.total;
          } else {
            this.tableData = [];
            this.totalAll = 0;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    search(value) {
      let val = value || 1;
      $http
        .get(`/unionMember/unionId/${this.unionId}/index/page?current=${val}&memberName=${this.input}`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
            this.tableData.forEach((v, i) => {
              v.member.createTime = timeFilter(v.createTime);
            });
            this.totalAll = res.data.data.total;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    handleCurrentChange(val) {
      this.search(val);
    },
    // 
    detail(scope) {
      $http
        .get(`/unionMember/${scope.row.member.id}/unionId/${this.unionId}`)
        .then(res => {
          if (res.data.data) {
            this.form = res.data.data;
            this.form.createTime = timeFilter(this.form.createTime);
            this.visible = true;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    remove(scope) {
      this.enterpriseName = scope.row.member.enterpriseName;
      this.memberId = scope.row.member.id;
      this.visible1 = true;
    },
    // 
    confirm1() {
      $http
        .post(`/unionMemberOut/unionId/${this.unionId}/applyMemberId/${this.memberId}`)
        .then(res => {
          if (res.data.success) {
            this.visible1 = false;
            this.$message({ showClose: true, message: '15', type: 'success', duration: 5000 });
            this.search();
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    withdraw(scope) {
      this.enterpriseName = scope.row.member.enterpriseName;
      this.outId = scope.row.memberOut.id;
      this.visible2 = true;
    },
    // 
    confirm2() {
      $http
        .del(`/unionMemberOut/${this.outId}/unionId/${this.unionId}`)
        .then(res => {
          if (res.data.success) {
            this.visible2 = false;
            this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
            this.search();
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    output() {
      let url =
        this.$store.state.baseUrl + `/unionMember/unionId/${this.unionId}/index/export?memberName=${this.input}`;
      window.open(url);
    }
  }
};
</script>


<style  lang='less' rel="stylesheet/less">
/*.el-table_1_column_6{*/

.model__10 {
  .el-dialog__body {
    padding: 0;
    margin-top: 15px;
    > div {
      margin: 20px 40px 50px;
      > span {
        float: right;
        margin-bottom: 20px;
      }
    }
    .el-form-item {
      margin-bottom: 17px !important;
    }
  }
  .el-dialog--tiny {
    width: 550px;
  }
  .el-form-item__label {
    width: 110px !important;
  }
}
</style>



